.BotPreview {
  background-color: white;
  margin-bottom: 16px;
  margin-right: 16px;
  padding: 16px;
  width: 500px;
}

.BotPreviewRow {
  width: 100%;
  display: flex;
}

.BotPreviewTableContainer {
  flex: 1;
}
.BotReturns {
  margin-top: auto;
}
.BotPreviewIconsSetContainer {
  margin-right: 16px;
  width: 110px;
  display: flex;
  flex-direction: column;
}
.BotInfoContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}
.BotInfoRecordContainer {
  display: flex;
  align-items: center;
  margin-bottom: 4px;
}
.BotInfoLabel {
  font-size: 12px;
  margin-right: 4px;
}
.BotInfoValue {
  font-size: 12px;
  font-weight: bold;
}

.IconsSetContainer {
  position: relative;
  width: 110px;
  height: 110px;
}

.RobotsIconContainer {
  position: absolute;
  top: 12px;
  left: 12px;
  border: 1px solid #c4c4c4;
  width: 84px;
  height: 84px;
  border-radius: 84px;
}
.RobotsIcon {
  position: absolute;
  top: 6px;
  left: 6px;
}

.BotTypeIconContainer {
  position: absolute;
  border: 1px solid #c4c4c4;
  width: 24px;
  height: 24px;
  border-radius: 24px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: bold;
}

.BotMarketIconContainer {
  position: absolute;
  right: 0;
  border: 1px solid #c4c4c4;
  width: 24px;
  height: 24px;
  border-radius: 24px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.BotMarketIcon {
  width: 16px;
  height: 16px;
  background-size: contain;
}

.BotNameContainer {
  display: flex;
  font-size: 18px;
  font-weight: bold;
  margin-bottom: 12px;
}
.BotName {
  width: 200px;
  flex: 1;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.BotReturnsContainer {
  width: 110px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.BotReturnsValue {
  font-size: 26px;
  font-weight: bold;
}

.BotReturnsLabel {
  font-size: 12px;
}
